.search-box {
  height: 60px;
  width: 100%;
  position: absolute;
  background: rgba(0, 0, 0, 0);
  box-sizing: border-box;
  left: 0px;
  top: 90px;
}

.search-box[status="1"] {
  transform: translateY(-40px);
  background: rgba(0, 0, 0, 0);
}

.search-box[sticky="true"],
.search-box[status="1"] {
  position: fixed;
  top: 40px;
  left: 0px;
  background: rgba(0, 0, 0, 0);
  border-bottom: var(--p-e5e5ea) 1px solid;
}

.search-cancel {
  position: absolute;
  top: 10px;
  right: 15px;
  width: 55px;
  height: 40px;
  opacity: 0;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  display: flex;
  color: var(--p-main-color);
  font-weight: 500;
  user-select: none;
  -webkit-user-select: none;
}

.search-box[status="1"] .search-cancel {
  opacity: 1;
}

.search {
  position: absolute;
  top: 10px;
  left: 15px;
  width: calc(100% - 30px);
  height: 40px;
  background: var(--p-e5e5ea);
  border-radius: var(--p-border-radius-default);
}

.search-box[sticky="true"] .search {
  background: rgba(var(--p-e5e5ea-r), var(--p-e5e5ea-g), var(--p-e5e5ea-b), 0.6);
}

.search-box[status="1"] .search {
  width: calc(100% - 95px);
  background: rgba(var(--p-e5e5ea-r), var(--p-e5e5ea-g), var(--p-e5e5ea-b), 0.6);
}

.search input#search {
  position: absolute;
  top: 5px;
  left: 40px;
  padding: 0px;
  margin: 0px;
  border-radius: 0px;
  border: none;
  width: calc(100% - 45px);
  height: calc(100% - 10px);
  font-size: 18px;
  background: rgba(0, 0, 0, 0);
  caret-color: var(--p-main-color);
  color: var(--p-333333);
}

.search input#search::placeholder {
  color: var(--p-aeaeb2);
  opacity: 1;
}

.search-icon {
  width: 40px;
  height: 40px;
  position: absolute;
  top: 0px;
  left: 0px;
  display: flex;
  justify-content: center;
  align-items: center;
  user-select: none;
  -webkit-user-select: none;
}

.search-icon svg {
  width: 23px;
  height: 23px;
}

.search-icon svg path {
  fill: var(--p-aeaeb2);
}

.search-output-box {
  position: fixed;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 100%;
  overflow: scroll;
  background: var(--p-ffffff);
  display: none;
  -webkit-overflow-scrolling: touch;
  overscroll-behavior: contain;
}

.search-output-box[status="1"] {
  display: block;
}

.search-output {
  width: 100%;
  display: inline-block;
  height: auto;
  position: relative;
  user-select: none;
  -webkit-user-select: none;
  margin-top: 60px;
}

.search-category {
  position: relative;
  width: 100%;
  height: 30px;
  user-select: none;
  -webkit-user-select: none;
}

.search-category-icon,
.search-category-preview {
  display: none;
  user-select: none;
  -webkit-user-select: none;
}

.search-category-title {
  position: absolute;
  top: 5px;
  left: 15px;
  height: 20px;
  width: calc(100% - 30px);
  display: flex;
  align-items: center;
  font-size: 14px;
  color: var(--p-333333);
  opacity: 0.7;
  user-select: none;
  -webkit-user-select: none;
}

.search-suggestion {
  position: relative;
  width: 100%;
  height: 45px;
  background: var(--p-f9f9fb);
  border-top: var(--p-e5e5ea) 1px solid;
  box-sizing: border-box;
  user-select: none;
  -webkit-user-select: none;
}

.search-output .search-suggestion:nth-child(2) {
  border-top: none;
}

.search-suggestion-icon {
  position: absolute;
  top: 7.5px;
  left: 20px;
  width: 30px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  user-select: none;
  -webkit-user-select: none;
}

.search-suggestion-icon svg {
  width: 23px;
  height: 23px;
  user-select: none;
  -webkit-user-select: none;
}

.search-suggestion-icon svg path {
  fill: var(--p-main-color);
}

.search-suggestion-title {
  position: absolute;
  top: 7.5px;
  left: 55px;
  width: calc(100% - 70px);
  height: 30px;
  display: flex;
  align-items: center;
  font-size: 18px;
  color: var(--p-main-color);
  user-select: none;
  -webkit-user-select: none;
}

.search-item {
  position: relative;
  width: 100%;
  height: 60px;
  background: var(--p-f9f9fb);
  border-bottom: var(--p-e5e5ea) 1px solid;
  box-sizing: border-box;
  user-select: none;
  -webkit-user-select: none;
}

.search-output .search-item:last-child {
  border-bottom: none;
}

.search-item-icon {
  position: absolute;
  top: 15px;
  left: 20px;
  width: 30px;
  height: 30px;
  user-select: none;
  -webkit-user-select: none;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-item-icon svg {
  width: 23px;
  height: 23px;
  user-select: none;
  -webkit-user-select: none;
}

.search-item-icon svg path {
  fill: var(--p-333333);
}

.search-item-title {
  position: absolute;
  top: 7.5px;
  left: 55px;
  width: calc(100% - 70px);
  height: 25px;
  display: flex;
  align-items: center;
  font-size: 18px;
  color: var(--p-333333);
  user-select: none;
  -webkit-user-select: none;
}

.search-item-preview {
  position: absolute;
  top: 32.5px;
  left: 55px;
  width: calc(100% - 70px);
  height: 20px;
  display: flex;
  align-items: center;
  font-size: 16px;
  color: var(--p-333333);
  user-select: none;
  -webkit-user-select: none;
  white-space: nowrap;
  word-break: keep-all;
  overflow: hidden;
}

.search-item-preview span.search-bold {
  font-weight: 700;
}